.form_field {
  margin-bottom: 1em;
}
.form_field > label {
  padding-top: 4px;
}
.form_field > .message.error {
  border: none;
  box-shadow: none;
  margin: 0;
  padding: 0;
}
#postbox-container-1 .postbox .form_field > label,
#postbox-container-2 .postbox .form_field > label {
  cursor: auto;
}
#postbox-container-1 .postbox .form_field > label.alignright,
#postbox-container-2 .postbox .form_field > label.alignright {
  text-align: right;
}
#postbox-container-1 .postbox .form_field > .input,
#postbox-container-2 .postbox .form_field > .input {
  display: block;
}
#postbox-container-1 .postbox .form_field > .input > textarea,
#postbox-container-2 .postbox .form_field > .input > textarea {
  resize: vertical;
}
#postbox-container-1 .postbox .form_field > .input .fullsize,
#postbox-container-2 .postbox .form_field > .input .fullsize {
  width: 100%;
}
#postbox-container-1 .postbox .form_field.fullsize > label,
#postbox-container-2 .postbox .form_field.fullsize > label {
  display: block;
  width: 100%;
  position: static;
  float: none;
}
#postbox-container-1 .postbox .form_field.fullsize > .input,
#postbox-container-2 .postbox .form_field.fullsize > .input {
  margin: 0;
  width: 100%;
}
#postbox-container-1 .postbox .form_field.fullsize > .input > input[type=text],
#postbox-container-2 .postbox .form_field.fullsize > .input > input[type=text],
#postbox-container-1 .postbox .form_field.fullsize > .input > input[type=password],
#postbox-container-2 .postbox .form_field.fullsize > .input > input[type=password],
#postbox-container-1 .postbox .form_field.fullsize > .input > select,
#postbox-container-2 .postbox .form_field.fullsize > .input > select {
  width: 100%;
}
#postbox-container-1 .postbox .form_field.fullsize > .input > textarea,
#postbox-container-2 .postbox .form_field.fullsize > .input > textarea {
  width: 100%;
}
#postbox-container-1 .postbox .form_field.fullsize > .input .ui-datepicker-month,
#postbox-container-2 .postbox .form_field.fullsize > .input .ui-datepicker-month,
#postbox-container-1 .postbox .form_field.fullsize > .input .ui-datepicker-year,
#postbox-container-2 .postbox .form_field.fullsize > .input .ui-datepicker-year {
  width: auto;
}
#postbox-container-1 .postbox .form_field.radio > .input,
#postbox-container-2 .postbox .form_field.radio > .input {
  padding-top: 5px;
}
#postbox-container-1 .postbox .form_field.radio > .input label,
#postbox-container-2 .postbox .form_field.radio > .input label {
  display: inline;
  float: none;
  font-size: 16px;
  line-height: 32px;
  min-height: 32px;
  white-space: nowrap;
  padding-right: 1em;
}
#postbox-container-1 .postbox .form_field.radio > .input label input[type=radio],
#postbox-container-2 .postbox .form_field.radio > .input label input[type=radio] {
  display: inline;
  float: none;
}
#postbox-container-1 .postbox .form_field.checkbox > label,
#postbox-container-2 .postbox .form_field.checkbox > label {
  display: none;
}
#postbox-container-1 .postbox .form_field.checkboxes > label,
#postbox-container-2 .postbox .form_field.checkboxes > label {
  white-space: nowrap;
  display: inline-block;
}
#side-sortables .form_field {
  margin-bottom: 1em;
}
#side-sortables .form_field > label {
  padding-top: 4px;
}
#side-sortables .form_field > .input > input[type=text],
#side-sortables .form_field > .input > input[type=password],
#side-sortables .form_field > .input > select {
  width: 100%;
}
#side-sortables .form_field > .input > textarea {
  width: 100%;
}
body,
html {
  height: auto;
}
.media_item.as-sortable-item,
.chayka-media_picker.multiple .media_items .as-sortable-placeholder {
  margin: 2px;
  background-size: cover;
  background-color: #e6e6e6;
  background-color: rgba(0, 0, 0, 0.1);
  display: block;
  float: left;
  border: 1px solid #888;
  box-sizing: border-box;
}
.media_item.as-sortable-item > .sortable-handle {
  position: relative;
  height: 100%;
  width: 100%;
}
.media_item.as-sortable-item > .sortable-handle > .btn_remove {
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
  color: white;
  background-color: maroon;
  border: none;
  border-radius: 3px;
  line-height: 8px;
  padding: 4px;
}
.media_item.contain {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}
.chayka-media_picker {
  position: relative;
  border: 1px dashed #888;
  text-align: center;
  min-height: 100px;
  padding: 0;
  overflow: hidden;
  background-color: #f0f0f0;
  background-color: rgba(0, 0, 0, 0.05);
  line-height: normal;
}
.chayka-media_picker > img {
  max-width: 100%;
  padding: 0;
  margin: 0 0 -4px;
  height: auto;
  width: auto;
  cursor: pointer;
}
.chayka-media_picker.multiple {
  padding: 2px;
  position: static;
}
.chayka-media_picker.multiple > .buttons {
  padding: 5px 0 0;
}
.chayka-media_picker.multiple > .media_items {
  text-align: left;
}
.chayka-media_picker.multiple > .media_items .as-sortable-placeholder {
  border: 1px dashed #888;
}
.chayka-media_picker > .no_image {
  cursor: pointer;
  height: 200px;
  background: url(../img/no-image.png) no-repeat center center;
  background-size: contain;
}
.chayka-media_picker > .no_image > [data-spinner] {
  position: relative;
  z-index: 2;
  top: 45%;
}
.chayka-media_picker > .no_image > [data-spinner] .chayka-spinner {
  background-color: #c8c8c8;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 5px 5px 5px 35px;
  border-radius: 3px;
  background-position: 5px center;
}
.chayka-media_picker > .note {
  clear: both;
}
.chayka-media_picker > .buttons {
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  text-align: right;
  top: 0;
  right: 0;
  padding: 5px;
  margin: 0;
}
.chayka-media_picker.image-set {
  border-style: solid;
}
#normal-sortables .form_field,
#advanced-sortables .form_field {
  margin-bottom: 1em;
}
#normal-sortables .form_field > label,
#advanced-sortables .form_field > label {
  padding-top: 4px;
}
#normal-sortables .form_field > .input,
#advanced-sortables .form_field > .input {
  display: block;
}
/* Job Control */
.chayka-job_control {
  border: 1px solid gray;
  box-sizing: border-box;
}
.chayka-job_control > .box_controls {
  margin: 0 1em 1em;
  height: 30px;
}
.chayka-job_control > .box_controls > button.dashicons-before {
  line-height: normal !important;
  padding: 0 4px !important;
  margin-right: 1ex;
}
.chayka-job_control > .box_controls .field_items_per_iteration {
  float: right;
}
.chayka-job_control > .box_controls .field_items_per_iteration input {
  width: 50px;
}
.chayka-job_control .progressbar {
  margin: 1em 1em 1em;
  height: 30px;
  border: 1px solid gray;
  box-sizing: border-box;
}
.chayka-job_control .progressbar .progress_label {
  float: left;
  width: 100%;
  margin-top: 5px;
  font-weight: bold;
  text-shadow: 1px 1px 0 #fff;
  text-align: center;
}
.chayka-job_control .progress_spinner {
  margin: 0 1em 1em;
  display: none;
}
.chayka-job_control .box_output {
  clear: both;
  margin: 0 1em 1em;
  height: 160px;
  overflow-y: scroll;
  border: 1px solid gray;
  box-sizing: border-box;
}
.chayka-job_control .box_output .message {
  padding: 0.5ex;
  border-bottom: 1px dashed #aaa;
  font-size: 13px;
}
.chayka-job_control .box_output .message:last-child {
  border-bottom: none;
}
/* Color Picker */
.form_field.color_picker > .input {
  position: relative;
  min-height: 32px;
}
.form_field.color_picker > .input > .wp-picker-container {
  position: absolute;
  z-index: 10;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-color-result {
  line-height: 16px;
  min-height: 32px;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-color-result::after {
  font-size: 14px;
  line-height: 30px;
  min-height: 32px;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-color-result.wp-picker-open + .wp-picker-input-wrap {
  display: inline-block;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-picker-input-wrap {
  width: 257px;
  padding: 10px;
  background-color: #ffffff;
  border: 1px solid #dfdfdf;
  box-sizing: border-box;
  display: none;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-picker-input-wrap > input.wp-color-picker {
  width: 85px;
  float: left;
  font-size: 16px;
  line-height: 16px;
  padding: 0 5px;
}
.form_field.color_picker > .input > .wp-picker-container > .wp-picker-input-wrap > input.button {
  font-size: 16px;
  line-height: 16px;
  min-height: 32px;
  float: right;
  padding: 0 8px 1px;
}
.form_field.color_picker.stretch > .input > .wp-picker-container {
  right: 0;
}
.form_field.color_picker.stretch > .input > .wp-picker-container > .wp-color-result {
  float: right;
  margin-right: 0;
}
.form_field.color_picker.stretch > .input > .wp-picker-container > .wp-picker-input-wrap {
  clear: both;
}
/* Thumbnails in theme-customized admin area */
.wp-list-table td > img {
  max-width: 100%;
  height: auto;
}
/* metabox fix */
.metabox > [data-form-validator],
.metabox > [form-validator] {
  width: auto;
}
